h5 {
	color: white;
	font-family: "Lobster", cursive;
	/* font-family: 'Source Sans Pro', sans-serif; */
}

.btn {
	z-index: 0;
	margin-top: 1%;
	background-color: maroon;
}

.logo-title {
	color: white;
	font-size: 40px;
	width: 90%;
	margin: 0 auto;
}

.logo-box {
	background-color: maroon;
	text-align: center;
	text-shadow: 2px 2px black;
	margin: 0.5% 0 0.5% 0;
	padding: 5%;
}

.show-title {
	background-color: hsl(208, 100%, 97%, 0.5);
	border: 2px solid black;
	text-align: center;
	margin: 0 auto;
	padding: 1%;
	width: 15%;
}

.custom-bg {
	margin-top: 15%;
}

.donation-image {
	margin: 0 auto;
	width: 76%;
	height: 500px;
	margin-top: -40%;
	background-color: rgb(0,0,0,0.4);
}